<template>
  <div class="p-5">
    <div class="container">
      <!--  Header  -->
      <div class="row">
        <div class="col-6">
          <h2 class="text-primary">Zoot Inventory</h2>
        </div>
        <form @submit="searchEan(ean)" class="col-6">
          <div class="row">
            <div class="col">
              <input
                type="text"
                v-model="ean"
                @keydown="validateInput()"
                placeholder="Interní ean"
                class="form-control"
                required
              >
            </div>
            <div class="col-4">
              <button type="submit" class="btn btn-primary btn-block">Potvrdit</button>
            </div>
          </div>
        </form>
      </div>
      <hr>
      <!--  Source data table  -->
      <div class="row">
        <div class="col-2">
          <h5 class="text-muted">Zdrojová data</h5>
        </div>
        <div class="col">
          <small>Počet záznamů: {{ source.length }}</small>
        </div>
      </div>
      <div class="source-table mb-3">
        <table class="table table-sm">
          <thead>
            <tr>
              <th>Interní Ean</th>
              <th>Množství</th>
              <th>Název</th>
              <th>Cena</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="row in source" :key="row.id">
              <td>{{ row.id }}</td>
              <td>{{ row.name }}</td>
              <td>{{ row.amount }}</td>
              <td>{{ row.price }} ,-</td>
            </tr>
          </tbody>
        </table>
      </div>
      <button :disabled="source.length == 0" class="btn btn-success">zkopírovat zbývající</button>
      <hr class="mt-5">
      <!--  Source data table  -->
      <div class="row">
        <div class="col-2">
          <h5 class="text-muted">Přebývá</h5>
        </div>
        <div class="col">
          <small>Počet záznamů: {{ over.length }}</small>
        </div>
      </div>
      <table class="table table-sm mb-5">
        <thead>
          <tr>
            <th>Interní Ean</th>
            <th>Množství</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="ean in over" :key="ean.ean">
            <td>{{ ean.ean }}</td>
            <td>{{ ean.amount}}</td>
            <td>
              <div class="btn-group float-right">
                <button class="btn btn-outline-secondary btn-sm">+</button>
                <button class="btn btn-outline-secondary btn-sm mr-3 ml-3">-</button>
                <button class="btn btn-outline-secondary btn-sm">odebrat</button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <button :disabled="over.length == 0" class="btn btn-success mb-5">zkopírovat přebytek</button>
      <!--  Source data table  -->
      <div class="row">
        <div class="col-2">
          <h5 class="text-muted">Nalezeno</h5>
        </div>
        <div class="col">
          <small>Počet záznamů: {{ matched.length }}</small>
        </div>
      </div>
      <table class="table table-sm">
        <thead>
          <tr>
            <th>Interní Ean</th>
            <th>Množství</th>
            <th>Název</th>
            <th>Cena</th>
          </tr>
        </thead>
        <tbody v-if="matched">
          <tr v-for="match in matched" :key="match.id">
            <td>{{ match.id }}</td>
            <td>{{ match.name }}</td>
            <td>{{ match.amount }}</td>
            <td>{{ match.price }} ,-</td>
          </tr>
        </tbody>
      </table>
      <button :disabled="matched.length == 0" class="btn btn-success">zkopírovat nalezené</button>
    </div>
  </div>
</template>

<script>
import * as inv from "@/utils/data/inventory";

export default {
  data() {
    return {
      source: inv.InventoryData,
      over: [],
      matched: [],
      ean: null
    };
  },
  methods: {
    searchEan(ean) {
      // Check for match

      let match;

      this.source.forEach((element, i) => {
        if (element.id == ean) {
          // Check quantity

          console.log(element);

          if (element.amount == 1) this.source.splice(i, 1);
          else element.amount--;

          match = { ...element };
          match.amount = 1;
        }
      });

      // If matched check for filtered matches and their quantity

      if (match) {
        let increased = false;

        this.matched.forEach(element => {
          if (element.id == match.id) {
            increased = true;
            element.amount++;
          }
        });

        if (!increased) this.matched.push(match);
      } else {
        let matched = false;

        const item = {
          ean: ean,
          amount: 1
        };

        this.over.forEach(element => {
          if (element.ean == item.ean) {
            element.amount++;
            matched = true;
          }
        });
        if (!matched) this.over.push(item);
      }
    },
    validateInput: function() {
      const value = event.target.value;
      const char = /^[0-9]*$"/;
      const letters = /^[A-Za-z]+$/;
      const pattern = /^[0-9]{10}$/;
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
.source-table {
  max-height: 200px;
  overflow-y: auto;
}
</style>
